import React, { Component } from "react";
import axios from "axios";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import validationTerimaSupplier from "../../Validations/validationTerimaSupplier";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;

class FormTransaksiTimbangbarang extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      status: "",
    };
  }

  componentDidMount() {
    axios
      .get(server + "subcategorys")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  onclick1() {
    this.props.change("murni", this.props.murni);
  }
  pilihBarang = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      namaJenis: result_explode[1],
      kadarCetak: result_explode[2],
      kadarJual: result_explode[3],
    });
  };

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarJual", this.state.kadarJual);
    this.props.change("revaluasi", this.props.revaluasi);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
              onChange={this.pilihBarang()}
              onBlur={this.setKadar()}
            >
              <option value="DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.kodeBarang === [] ? (
                <option>TIDAK ADA DATA</option>
              ) : (
                this.state.kodeBarang.map((kode) => (
                  <option
                    value={
                      kode.kodeJenis +
                      "|" +
                      kode.namaJenis +
                      "|" +
                      kode.kadarCetak +
                      "|" +
                      kode.kadarJual
                    }
                    key={kode.kodeJenis}
                  >
                    {kode.namaJenis}
                  </option>
                ))
              )}
            </Field>
          </div>
          <div className="col-md-8">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="kadar"
              label="Kadar (%)"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="pkg"
              label="Bungkus (Gr)"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="bruto"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.props.change("murni", this.props.murni)}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="kadarJual"
              label="Harga Jual"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="revaluasi"
              label="Revaluasi"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTransaksiTimbangbarang = reduxForm({
  form: "FormTransaksiTimbangbarang",
  validate: validationTerimaSupplier,
  enableReinitialize: true,
})(FormTransaksiTimbangbarang);
const selector = formValueSelector("FormTransaksiTimbangbarang"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarJual } = selector(state, "bruto", "kadarJual");
  return {
    revaluasi: (parseFloat(bruto) * (parseFloat(kadarJual) / 100)).toFixed(3),
  };
})(FormTransaksiTimbangbarang);
