import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import Skeleton from "react-loading-skeleton";
import { connect } from "react-redux";
import SelectSearch from "react-select-search";
import { reduxForm, reset } from "redux-form";
import Swal from "sweetalert2";
import { getListTimbangBarang } from "../actions/timbangBarangAction";
import validaitonTimbangBarang from "../Validations/validationTimbangBarang";
import FormTranskaksiTimbangBarang from "./Component/FormTranskaksiTimbangBarang";

const maptostate = (state) => {
  return {
    timbangBarang: state.provinsi.timbangBarang,
  };
};
class TransaksiTimbangBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kodeBarang",
          text: "Kode Barang",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "kadar",
          text: "Kadar(%)",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "kadarJual",
          text: "Harga(%)",
        },
        {
          dataField: "revaluasi",
          text: "Netto(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListTimbangBarang());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handleSubmit(data) {
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFUALT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var simpan = {
      bruto: data.bruto,
      kadar: data.kadar,
      kadarJual: data.kadarJual,
      keterangan: split[1],
      kodeBarang: split[0],
      pkg: data.pkg,
      revaluasi: data.revaluasi,
    };
    var array = JSON.parse(localStorage.getItem("timbangBarang")) || [];
    array.push(simpan);
    localStorage.setItem("timbangBarang", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListTimbangBarang());
    document.getElementById("dismissModal").click();
    this.props.dispatch(reset("FormTransaksiTimbangbarang"));
  }
  timbangBarangSubmit() {
    var simpan = {
      tanggal: this.state.tanggal,
      namaToko: this.state.namaToko,
      detail: this.state.detail,
      barang: JSON.parse(localStorage.getItem("timbangBarang")),
    };
    console.log(simpan);
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="col-lg-12 col-md-12">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">Tanggal</label>
                  <input
                    name="tanggal"
                    id="tanggal"
                    type="date"
                    className="form-control"
                    onChange={this.inputChange("tanggal")}
                  ></input>
                </div>
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">Nama Toko</label>
                  <SelectSearch
                    options={this.state.items}
                    search
                    placeholder="Silahkan Pilih"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">Detail</label>
                  <input
                    name="detail"
                    id="detail"
                    type="text"
                    className="form-control"
                    onChange={this.inputChange("detail")}
                  ></input>
                </div>
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="row">
                    <div className="col-md-6">
                      <div className="text-left">
                        <button
                          className="btn btn-primary mr-3"
                          onClick={() => this.timbangBarangSubmit()}
                        >
                          Simpan
                          <i class="fa fa-paper-plane ml-2" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-target="#primary-header-modal"
                          data-toggle="modal"
                        >
                          Tambah Data
                          <i class="fa fa-plus ml-2" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 mt-5">
                  {this.props.timbangBarang ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.timbangBarang}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <Skeleton width={"100%"} height={100} />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Transaksi Timbang Barang
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormTranskaksiTimbangBarang
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

TransaksiTimbangBarang = reduxForm({
  form: "TransaksiTimbangBarang",
  enableReinitialize: true,
  validate: validaitonTimbangBarang,
})(TransaksiTimbangBarang);
export default connect(maptostate, null)(TransaksiTimbangBarang);
